@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, css, #root {
  min-height: 100%;
}

body {
  background: linear-gradient(45deg, #020024 0%, #090979 0%, #247BB3 100%);
  -webkit-font-smoothing: antialiased !important;
}

body, input, button {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 14px;
  text-align: center;
}

button {
  border: 0;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  font-weight: bold;
}

.title {
  margin: 10px;
}

.container {
  display: flex;
  flex-direction: column;
  margin: 40px auto 0;
  width: 100%;
  max-width: 450px;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}

.game-img {
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 10px;
  border-radius: 10px;
}

.subtitle {
  font-size: 16px;
  margin-top: 10px;
}

.game-desc {
  color: #444;
  margin: 10px;
  width: 100%;
  max-width: 350px;
  text-align: justify;
}

button.play {
  color: #eee;
  background-color: #ff4040;
}

button.play:hover {
  background-color: #ff3030;
}
@-webkit-keyframes grow {
  from {
    font-size: 18;
  }
  to {
    font-size: 20px;
  }
}

@keyframes grow {
  from {
    font-size: 18;
  }
  to {
    font-size: 20px;
  }
}

* {
  color: #333;
}

.game-window {
  background: rgb(2,0,36);
  background: linear-gradient(45deg, rgba(2,0,36,1) 0%, #095079 1%, #26ABFF 100%);
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 10px;
  border-radius: 10px;
  padding: 10px;
  align-items: center;
}

p {
  margin: 40px;
  font-size: 18px;
}

p.word {
  color: #222;
  font-size: 36px;
  font-family: 'Fredoka One', Arial, Helvetica, sans-serif;
}

button.game-btn {
  margin-top: 20px;
  color: #eee;
  background-color: #ff4040;
  width: 125px;
}

button.game-btn:hover {
  background-color: #ff3030;
}

button.back {
  background-color: #ccc;
}

button.back:hover {
  background-color: #bbb;
}

p.counter span {
  margin: 5px;
  height: 20px;
}

p.counter span.count {
  -webkit-animation-name: grow;
          animation-name: grow;
  -webkit-animation-duration: 250ms;
          animation-duration: 250ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  color: #ff0000;
}
button.retry {
  color: #eee;
  background-color: #ff4040;
}

button.retry:hover {
  background-color: #ff3030;
}
